/* eslint-disable react-hooks/rules-of-hooks */
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { register } from '../actions/userActions';
import LoadingBox from '../Home/LoadingBox';
import MessageBox from '../Home/MessageBox';

const Register = (props) => {
    const [email,setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [name, setName] = useState('');

    const redirect = props.location.search ? props.location.search.split('=')[1]:'/'; 

    const userRegister = useSelector((state)=>state.userRegister);
    const {userInfo,loading,error} = userRegister;

    const dispatch = useDispatch();

    const submitHandler =async(e)=>
    {
        e.preventDefault();
        if(password !== confirmPassword)
        {
            alert("passwords don't match")
        }
        else{
       
       await dispatch(register(name,email,password));
     
        }
        
    };

    useEffect(()=>
    {
        if(userInfo)
        {
            props.history.push(redirect);
            
        }
    },[props.history,redirect,userInfo]);

  
    return (
        <div >
            <form className="form" onSubmit={submitHandler}>
                <div>
                    <h1>Register</h1>
                </div>
                {loading && <LoadingBox></LoadingBox>}
                {error && <MessageBox variant="danger">{error}</MessageBox>}
                <div>
                    <label htmlFor="name">Enter Name</label>
                    <input type="text"
                     id="name" placeholder="Enter name"
                      required onChange={e=>setName(e.target.value)}>

                    </input>
                    
                </div>
                <div>
                    <label htmlFor="email">Email address</label>
                    <input type="email"
                     id="email" placeholder="Enter email"
                      required onChange={e=>setEmail(e.target.value)}>

                    </input>
                    
                </div>
                <div>
                <label htmlFor="password">Password</label>
                    <input type="password"
                     id="password" placeholder="Enter password"
                      required onChange={e=>setPassword(e.target.value)}>

                    </input>
                </div> 
                <div>
                <label htmlFor="confirmPassword">Confirm Password</label>
                    <input type="password"
                     id="confirmPassword" placeholder="Confirm password"
                      required onChange={e=>setConfirmPassword(e.target.value)}>

                    </input>
                </div> 
                <div>
                    <label/>
                    <button className="primary" type="submit">Sign Up</button>
                </div>
                <div>
                    <label/>
                    <h4>Have an account ?  <Link  to ={`/signin?redirect=${redirect}`}>Sign In</Link></h4> 
                  
                </div>
            </form>
            
        </div>
    )
}

export default Register;  
